html {
  box-sizing: border-box;
  font-size:16px;
}
*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

ul {
  padding: 0;
}

.answer-buttons button {
  width: 100%;
text-align: left;
background: transparent;
border: none;
cursor: pointer;
margin: 0 0 1em;
line-height: 1.2;
padding: 1em;
font-size:16px;
border:1px solid #777;
border-radius:1em;
}

button:hover {
  background: #ddf;
}

button.correctAnswer {
  background: #afa;
}
button.wrongAnswer {
  background: red;
  color:white;
}

.advance-button {
  float:right;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size:16px;
  border:1px solid #777;
  border-radius:1em;
  padding: 1em;
  line-height: 1.2;
}
.advance-button:hover {
  background: #ddf;
}